import React from 'react';
import { connectRedux } from '../../common/redux';

const Attack = ({attack}) => {
    return (
        <div className='attack'>
            {attack.name}
        </div>
    );
};

const Infobar = ({board}) => {
    let unit = board.selectedUnit;
    if(!unit) return null;
    return (
        <div>
            <h3>{unit.name}</h3>
            <div>Health: {unit.tiles.length}</div>
            <div>Max Health: {unit.maxLength}</div>
            <div>Move: {unit.moves}</div>
            {unit.getAttacks().map((attack, index) => <Attack key={index} attack={attack} />)}
        </div>
    );
};

export default connectRedux(({board})=>({board}))(Infobar);
